import React from "react";
import ProjectCardFilter from "./ProjectCardFilter";

function ProjectWrap(props) {
  return (
    <>
      <div className="project-area sec-mar">
        <div className="container">
          <div className="title-wrap">
            <div className={`${"sec-title"} ${props.white}`}>
              <span>Proiecte SOFTSITE</span>
              <h2>Portofoliu Proiecte</h2>
              <p>
              Oferim o gam complet de servicii IT, noi suntem partenerul firmei tale. Oferim profesionalism i seriozitate n orice colaborare. V putem dezvolta, automatiza i promova afacerea.
              </p>
            </div>
          </div>
          <ProjectCardFilter/>
        </div>
      </div>
    </>
  );
}

export default ProjectWrap;
